import { useEffect, useRef } from "react";

export default function Home() {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    const binaryChars = "010101010101".split("");
    const fontSize = 16;
    const columns = Math.floor(canvas.width / fontSize);
    const drops = Array(columns).fill(1);

    function draw() {
      ctx.fillStyle = "rgba(0, 0, 0, 0.1)";
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      ctx.fillStyle = "#00ff00";
      ctx.font = `${fontSize}px monospace`;

      for (let i = 0; i < drops.length; i++) {
        const text = binaryChars[Math.floor(Math.random() * binaryChars.length)];
        ctx.fillText(text, i * fontSize, drops[i] * fontSize);

        if (drops[i] * fontSize > canvas.height && Math.random() > 0.975) {
          drops[i] = 0;
        }
        drops[i]++;
      }
    }

    const interval = setInterval(draw, 50);
    return () => clearInterval(interval);
  }, []);

  return (
    <div style={{ position: "relative", overflow: "hidden", height: "100vh", background: "black" }}>
      <canvas ref={canvasRef} style={{ position: "absolute", top: 0, left: 0 }} />
      <div style={{ position: "relative", zIndex: 2, textAlign: "center", paddingTop: "20vh" }}>
        <h1 style={{ fontSize: "3rem", fontFamily: "'Comic Sans MS', cursive", color: "#ff5722" }}>
          Welcome to Cousin Chronicles
        </h1>
        <p style={{ fontSize: "1.5rem" }}>
          Visit{' '}
          <a href="/gallery" style={{ color: "#4ecdc4", textDecoration: "underline" }}>
            the gallery
          </a>{' '} to see the fun!
        </p>
      </div>
    </div>
  );
}